import React from 'react'
import coder from "../images/coder.svg";


function Compo2() {
  return (
    <div className='coderimg'>
      <img src={coder} width='545px' />
      <div className="code">
        <h1 className='h1' >Our Coding Base</h1>
        <h4> Only Server side Laptops And PCs.</h4>
      </div>
    </div>
  )
}

export default Compo2
